<template>
    <Navbar>
      <template #logo>
        <NavbarLogo link="https://www.google.com/" alt="Flowbite logo" image-url="https://flowbite.com/docs/images/logo.svg">
          Flowbite
        </NavbarLogo>
      </template>
      <template #default="{isShowMenu}">
        <NavbarCollapse :isShowMenu="isShowMenu">
          <router-link isActive to="/">Home</router-link>
          <router-link to="/orderdetails">Order Details</router-link>
          <!-- <NavbarLink link="#">Pricing</NavbarLink>
          <NavbarLink link="#">Contact</NavbarLink> -->
          <li>
            <button @click.prevent="toggleDropdown" data-dropdown-toggle="dropdownNavbar" class="flex items-center justify-between w-full py-2 pl-3 pr-4 text-gray-900 rounded hover:bg-gray-100 md:hover:bg-transparent md:border-0 md:hover:text-blue-700 md:p-0 md:w-auto dark:text-white md:dark:hover:text-blue-500 dark:focus:text-white dark:border-gray-700 dark:hover:bg-gray-700 md:dark:hover:bg-transparent">Dropdown <svg class="w-5 h-5 ml-1" aria-hidden="true" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" d="M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z" clip-rule="evenodd"></path></svg></button>
            <!-- Dropdown menu -->
            <div style="position:absolute;z-index:999;top:5%" v-if="toggle" id="dropdownNavbar" class="z-10  font-normal bg-white divide-y divide-gray-100 rounded-lg shadow w-44 dark:bg-gray-700 dark:divide-gray-600">
                <ul class="py-2 text-sm text-gray-700 dark:text-gray-400" aria-labelledby="dropdownLargeButton">
                  <li>
                    <a @click.prevent="changeLang('ja')" href="#" :class="{'bg-gray-100':$i18n.locale == 'ja' }" class="block px-4 py-2 hover:bg-gray-100 dark:hover:bg-gray-600 dark:hover:text-white">Chinese</a>
                  </li>
                  <li>
                    <a @click.prevent="changeLang('en')" href="#" :class="{'bg-gray-100':$i18n.locale == 'en' }" class="block px-4 py-2 hover:bg-gray-100 dark:hover:bg-gray-600 dark:hover:text-white">English</a>
                  </li>
                </ul>
                <div class="py-1">
                  <a v-if="$store.state.core.loggedIn" href="#"  @click.prevent="signOut" class="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-100 dark:hover:bg-gray-600 dark:text-gray-400 dark:hover:text-white">Sign out</a>
                </div>
            </div>
        </li>
        </NavbarCollapse>
      </template>
    </Navbar>
</template>
<script>
/* eslint-disable */
import { Navbar, NavbarLogo, NavbarCollapse, Toggle} from 'flowbite-vue'
export default { 
  data() {
    return {
      toggle:false
    }
  },
  methods: {
    toggleDropdown() {
      this.toggle = !this.toggle
    },
    changeLang(value) {
     this.$store.commit("lang",value)
     this.$i18n.locale = value
     this.toggleDropdown()
    },
    signOut() {
      this.toggleDropdown()
      this.$store.commit("loggedIn",false)
      this.$store.commit("token",null)
      this.$router.push('/admin-login')
    }
  },
  components:{
    Navbar,
    NavbarLogo,
    NavbarCollapse,
    Toggle
  }
}
</script>
