<template>
<PageLoader
    v-if="loading"
  />
   <div class="relative mx-auto sm:max-w-xl md:max-w-full  mt-8 mb-8" style="max-width:80%;min-height:900px">
    <center class="mb-12 mt-12">welcome ot dashboarddd</center>

     <EasyDataTable
        buttons-pagination
          border-cell
          :alternating="true"
          theme-color="red"
          rows-per-page="5"
          :headers="headers"
          :items="items"
          search-field="fullname"
          :search-value="searchValue"
        >
        
        <template #item-operation="item">
          <div class="operation-wrapper">
            <router-link style="float:left" :to="item.fullname"><Icon icon="mdi-light:file-multiple" height="24" :inline="true"/></router-link>
            </div>
            <a style="float:left" href="#" ><Icon icon="mdi-light:delete" height="24" :inline="true" /></a>
        </template>
        <template #item-orderStatus="item">
          <span v-if="!item.isPaid" class="bg-red-100 text-red-800 text-xs font-medium mr-2 px-2.5 py-0.5 rounded dark:bg-red-900 dark:text-red-300">Payment pending</span>
          <span v-else class="bg-green-100 text-green-800 text-xs font-medium mr-2 px-2.5 py-0.5 rounded dark:bg-red-900 dark:text-red-300">Payment successfull</span>

          <span v-if="item.Logistics.id" class="bg-green-100 text-green-800 text-xs font-medium mr-2 px-2.5 py-0.5 rounded dark:bg-green-900 dark:text-green-300">Logistics setup complete</span>
          <span v-else class="bg-red-100 text-red-800 text-xs font-medium mr-2 px-2.5 py-0.5 rounded dark:bg-green-900 dark:text-green-300">Logistics setup pending</span>
        </template>
        </EasyDataTable>
  </div>
  
</template>

<script>
/* eslint-disable */

import { Icon } from '@iconify/vue';
import PageLoader from '@/components/PageLoader.vue'

export default {
  components:{
    Icon,
    PageLoader
  },
  data(){
    return {
      loading:false,
      searchValue:"",
      headers:[
        { text: "Name", value: "fullname" },
        { text: "Email", value: "email"  },
        { text: "Order Status", value: "orderStatus" },
        { text: "Operation", value: "operation" }
      ],
       items : []
    }
  },
  async mounted() {
    try{
      this.loading = true;
      let {data} = await this.axios.get('admin/all-orders')
      this.items = data.data
      this.loading = false;
      
    }catch(e) {
      this.loading = false;
      console.log(e)
      let { data : { message } } = e.response
      this.error = message
      this.$notify({ title: "Error", text: message });
    }
  }
}
</script>
