<template>
  <PageLoader
    v-if="loading"
  />
  <div class="relative mx-auto sm:max-w-xl md:max-w-full  mt-8 mb-8" style="max-width:80%">
    <div class="renderedBanner" ref="renBanner"></div>
    <form class="w-full  mt-12">
      <div class="flex flex-wrap -mx-3 mb-6">
        <div class="w-full md:w-1/2 px-3 mb-6 md:mb-0">
          <label class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2">
            Full Name
          </label>
          <input v-model="form.fullname" class="appearance-none block w-full bg-gray-200 text-gray-700 border  rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white" :class="{ 'border-red-500': form.fullname.length < 5 && formTouched }"  type="text" placeholder="Full Name">
          <p v-if="form.fullname.length < 5 && formTouched" class="text-red-500 text-xs italic">Please fill out this field.</p>
        </div>
        <div class="w-full md:w-1/2 px-3">
          <label class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2">
            Email
          </label>
          <input v-model="form.email" class="appearance-none block w-full bg-gray-200 text-gray-700 border border-gray-200 rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 mb-3" :class="{ 'border-red-500': !checkMail && formTouched }"  type="email" placeholder="Email">
          <p v-if="!checkMail && formTouched" class="text-red-500 text-xs italic">Please fill out this field.</p>
        </div>
      </div>
      <div class="flex flex-wrap -mx-3 mb-2">
        <div class="w-full md:w-1/3 px-3 mb-6 md:mb-0">
          <label class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="grid-city">
            Phonenumber
          </label>
          <input v-model="form.phonenumber" class="appearance-none block w-full bg-gray-200 text-gray-700 border border-gray-200 rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500 mb-2" :class="{ 'border-red-500': form.phonenumber.length < 5 && formTouched }" type="text" placeholder="Phonenumber">
          <p v-if="form.phonenumber.length < 5 && formTouched" class="text-red-500 text-xs italic">Please fill out this field.</p>
        </div>
        <div class="w-full md:w-1/3 px-3 mb-6 md:mb-0">
          <label class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="grid-state">
            Delivery method
          </label>
          <div class="relative">
            <select v-model="form.deliveryMethod" class="block appearance-none w-full bg-gray-200 border border-gray-200 text-gray-700 py-3 px-4 pr-8 rounded leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-state">
              <option value="home">Home Delivery</option>
              <option value="pickup">Pick Up</option>
            </select>
          </div>
        </div>
        <div class="w-full md:w-1/3 px-3 mb-6 md:mb-0">
          <label class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2" for="grid-zip">
            Quantity
          </label>
          <input v-model="form.quantity" class="appearance-none block w-full bg-gray-200 text-gray-700 border border-gray-200 rounded py-3 px-4 leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-zip" type="number" placeholder="Quantity">
        </div>
      </div>
        <div class=" mt-12 flex flex-wrap -mx-3 mb-6">
        <div class="w-full px-3">
          <label class="block uppercase tracking-wide text-gray-700 text-xs font-bold mb-2">
            Delivery address
          </label>
          <textarea v-model="form.address" class="appearance-none block w-full bg-gray-200 text-gray-700 border border-gray-200 rounded py-3 px-4 mb-3 leading-tight focus:outline-none focus:bg-white focus:border-gray-500" id="grid-password" type="password" placeholder="******************"></textarea>
             <p v-if="form.address.length < 30 && formTouched" class="text-red-500 text-xs italic">Please fill out this field.</p>
        </div>
      </div>
    </form>
    <a v-if="$store.state.core.banner.step == 4" @click.prevent="saveBanner" href="#" aria-current="page" class="px-4 py-2 text-sm font-medium text-blue-700 bg-white border border-gray-200 rounded-l-lg hover:bg-gray-100 focus:z-10 focus:ring-2 focus:ring-blue-700 focus:text-blue-700 dark:bg-gray-700 dark:border-gray-600 dark:text-white dark:hover:text-white dark:hover:bg-gray-600 dark:focus:ring-blue-500 dark:focus:text-white">
    Save banner & continue
  </a>
  </div>
  
</template>
<script>
/* eslint-disable */
import PageLoader from '@/components/PageLoader.vue'
export default {
  components:{
    PageLoader
  },
  data() {
    return {
      renderHeight:null,
      elements:{},
      formTouched:false,
      form:{
        fullname:'',
        email:'',
        deliveryMethod:'home',
        phonenumber:'',
        quantity:1,
        address:'',
      },
      loading:false
    }
  }, 
  mounted() {
    let savedBanner = this.$store.state.core.savedBanner
    this.elements = savedBanner
    console.log(this.elements)
    this.renderPreviewHTML()
  },
  computed:{
    checkMail() {
      var sQtext = '[^\\x0d\\x22\\x5c\\x80-\\xff]';
      var sDtext = '[^\\x0d\\x5b-\\x5d\\x80-\\xff]';
      var sAtom = '[^\\x00-\\x20\\x22\\x28\\x29\\x2c\\x2e\\x3a-\\x3c\\x3e\\x40\\x5b-\\x5d\\x7f-\\xff]+';
      var sQuotedPair = '\\x5c[\\x00-\\x7f]';
      var sDomainLiteral = '\\x5b(' + sDtext + '|' + sQuotedPair + ')*\\x5d';
      var sQuotedString = '\\x22(' + sQtext + '|' + sQuotedPair + ')*\\x22';
      var sDomain_ref = sAtom;
      var sSubDomain = '(' + sDomain_ref + '|' + sDomainLiteral + ')';
      var sWord = '(' + sAtom + '|' + sQuotedString + ')';
      var sDomain = sSubDomain + '(\\x2e' + sSubDomain + ')*';
      var sLocalPart = sWord + '(\\x2e' + sWord + ')*';
      var sAddrSpec = sLocalPart + '\\x40' + sDomain; // complete RFC822 email address spec
      var sValidEmail = '^' + sAddrSpec + '$'; // as whole string

      var reValidEmail = new RegExp(sValidEmail);

      return reValidEmail.test(this.form.email);
    },
  },
  methods: {
    validateForm() {
      if(this.form.fullname.length < 5) {
        return false
      }
      if(!this.checkMail) {
        return false
      }
      if(this.form.phonenumber.length < 5) {
        return false
      }
      if(this.form.address.length < 30) {
        return false
      }
      return true
    },
    async saveBanner() {
       this.formTouched = true
       if(!this.validateForm()){
         this.$notify({ title: "Alert!", text: 'Please fillout the required information' });
         return 
       }
      try{
        this.loading = true;
        let formData = this.form
        formData.bannerData = this.$store.state.core.savedBanner
        let {data} = await this.axios.post('orders',formData)
        this.loading = false;
        this.$store.commit("savedBanner",null)
        this.$store.commit("font",'Shojumaru')
        this.$store.commit("banner",{step:1})
       this.$router.push(`/ordercreated/${data.id}`)
        console.log(data)
      }catch(e) {
        this.loading = false;
        console.log(e)
        // let { data : { message } } = e.response
        // this.error = message
        this.$notify({ title: "Error", text: 'there was an error' });
      }
    },
    renderPreviewHTML() {
      let dimensions = this.elements.dimensions
      let canvasWrapper = this.$refs.renBanner.clientWidth
      let ratio = canvasWrapper/dimensions.width
      let calcHeight = ratio * dimensions.height
      this.renderHeight = calcHeight

      let html = ''
      html += `<div class="renverPreview" style="height:${this.renderHeight}px;width:${canvasWrapper}px;background-color:${this.elements.background}">`
      this.elements.elements.forEach((ele)=>{
        let ratialFontSize = this.renderHeight/ele.fontRatio
        let fontFamily = ele.styles['font-family']
        html += `<p  class="banner-item" 
        style="font-size: ${ratialFontSize}rem; color: ${ele.styles.color}; font-family: ${fontFamily}; top: ${ele.top}%;">${ele.text}</p>`
      })
      html +=  '</div>';
      this.$refs.renBanner.innerHTML =  html 
      console.log(this.elements)
    },
  }
}
</script>
